import TabBar from './TabBar'
import {listData} from '../../server/employee'
export default {
    name:'employee',
    components:{TabBar},
    data(){
        return{
            dataList:[],
            loading:true,
            imgUrl:require("@/assets/img/Group.png"),
            page:{
                limit:50,
                page:1
            }
        }
    },
    created(){
        this.getList()
    },
    methods:{
        getList(){
            listData({
                page:this.page.page,
                limit:this.page.limit
            }).then(e=>{
                if(e.data.code){
                    console.log(e);
                }
            })
        },
       handlePush(){
           this.$router.push({path:'/AddEmployee'})
       },
       loadMore() {
            this.loading = true;
            // setTimeout(() => {
            let last = this.list[this.list.length - 1];
            for (let i = 1; i <= 9; i++) {
                this.list.push(last + i);
            }
            this.loading = false;
            // }, 2500);
        }
    }
}



